<template>
  <Header/>
  <router-view/>
  <Footer/>
  <Modal/>
</template>

<script setup>
import Header from "@/components/header/_index.vue"
import Footer from "@/components/footer/_index.vue"
import Modal from "@/components/common/modal-common.vue"
import { onBeforeMount  } from 'vue';
import { useStore } from 'vuex';

const store = useStore();
onBeforeMount (() => {
  window.addEventListener('resize', handleResize)
  handleResize()
})

const handleResize = () => {
  let screenWidth = window.innerWidth
  if( screenWidth <= 768){
    store.commit("SET_IS_SCREEN_MOBILE", true);
  }else {
    store.commit("SET_IS_SCREEN_MOBILE", false);
  }
  console.log(store.state.isScreenMobile);
}
</script>

