<template>
  <button
    :class="buttonClasses"
    :disabled="isLoading || isDisable"
    @click="actionButton"
  >
    {{ props.text }}
  </button>
</template>

<script setup>
import { defineProps } from "vue";
const props = defineProps({
  type: {
    type: String,
    default: "default",
  },
  text: {
    type: String,
    default: "",
  },
  className: {
    type: String,
    default: "",
  },
  isNoHover: {
    type: Boolean,
    default: false,
  },
  isLoading: {
    type: Boolean,
    default: false,
  },
  isDisable: {
    type: Boolean,
    default: false,
  },
  actionButton: Function
});
const buttonClasses = `button button__${props.type} ${
  props.className !== "" ? ` ${props.className}` : ""
} ${props.isNoHover ? "" : "hover"}`;

</script>

<style lang="scss" scoped src="@/assets/scss/common/button/_index.scss"></style>