<template>
  <nav
    class="navbar navbar-expand-lg navbar-dark fixed-top"
    :class="{ 'navbar-shrink': scrollPosition > 0 }"
    id="mainNav"
  >
    <div class="container">
      <a class="navbar-brand" href="#page-top"
        ><img src="@/assets/img/logoLngang.png" alt="..."
      /></a>
      <button class="navbar-toggler" type="button" @click="handleShowMennu">
        Menu
        <i class="fas fa-bars ms-1"></i>
      </button>
      <div
        class="collapse navbar-collapse"
        :class="{ show: showMenu }"
        id="navbarResponsive"
      >
        <ul class="navbar-nav text-uppercase ms-auto py-4 py-lg-0">
          <li class="nav-item">
            <a href="#" class="nav-link">Trang chủ</a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              href="#services"
              :class="{ active: activeSections.includes('services') }"
              >Dịch vụ</a
            >
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              href="#portfolio"
              :class="{ active: activeSections.includes('portfolio') }"
              >Dự án</a
            >
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              href="#news"
              :class="{ active: activeSections.includes('news') }"
              >Tin tức</a
            >
          </li>
          <li class="nav-item">
            <a
              class="nav-link"
              href="#contact"
              :class="{ active: activeSections.includes('contact') }"
              >Liên hệ</a
            >
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from "vue";
const scrollPosition = ref(0);
const activeSections = ref([]);
let showMenu = ref(false);

const handleIntersect = (entries) => {
  entries.forEach((entry) => {
    if (entry.isIntersecting) {
      if (!activeSections.value.includes(entry.target.id)) {
        activeSections.value.push(entry.target.id);
      }
    } else {
      activeSections.value = activeSections.value.filter(
        (id) => id !== entry.target.id
      );
    }
  });
};

const handleScroll = () => {
  scrollPosition.value = window.pageYOffset || window.scrollY;
};

onMounted(() => {
  window.addEventListener("scroll", handleScroll);
  document.querySelectorAll("li>a.nav-link").forEach((item) => {
    item.addEventListener('click', ()=> {
      handleShowMennu()
    })
  })
  window.onload = () => {
    const options = {
      root: null,
      rootMargin: "10px",
      threshold: 0.35,
    };

    const observer = new IntersectionObserver(handleIntersect, options);
    const sectionIds = ["services", "portfolio", "news", "contact"];
    sectionIds.forEach((sectionId) => {
      const section = document.getElementById(sectionId);
      if (section) {
        observer.observe(section);
      }
    });
  };
});
onUnmounted(() => {
  window.removeEventListener("scroll", handleScroll);
});
const handleShowMennu = () => {
  showMenu.value = !showMenu.value;
};
</script>

<style lang="scss" src="@/assets/scss/header/_index.scss"></style>
