<template>
  <transition-group name="fade" mode="out-in">
    <div class="portfolio-modal modal" v-if="showModal">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="close-modal" data-bs-dismiss="modal" @click="onHideModal">
            <img src="@/assets/img/close-icon.svg" alt="Close modal" />
          </div>
          <div class="container">
            <div class="row justify-content-center">
              <div class="col-lg-8">
                <div class="modal-body">
                  <!-- Project details-->
                  <h2 class="text-uppercase">{{ dataModal.title }}</h2>
                  <p class="item-intro text-muted">
                    {{ dataModal.textIntro }}
                  </p>
                  <img
                    class="img-fluid d-block mx-auto"
                    :src="dataModal.img"
                    alt="..."
                  />
                  <p>
                    {{ dataModal.description }}
                  </p>
                  <ul class="list-inline">
                    <li>
                      <strong>Client:</strong>
                      {{ dataModal.title }}
                    </li>
                    <li>
                      <strong>Category:</strong>
                      {{ dataModal.text }}
                    </li>
                  </ul>
                  <button
                    class="btn btn-primary btn-xl text-uppercase"
                    data-bs-dismiss="modal"
                    type="button"
                    @click="onHideModal"
                  >
                    <i class="fas fa-xmark me-1"></i>
                    Close Project
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition-group>
</template>
<script setup>
import { ref, onMounted, onUnmounted, reactive } from "vue";
import eventBus from "@/utils/eventBus"



const showModal = ref(false);
const dataModal = reactive({
  title:'',
  img:'',
  text:'',
  textIntro:'',
  description:''
})

const onShowModal = (data) => {
    document.body.classList.add('modal-open');
    showModal.value = !showModal.value;

    dataModal.title = data.title;
    dataModal.img = data.img;
    dataModal.text = data.text;
    dataModal.textIntro = data.textIntro;
    dataModal.description = data.description;
}
const onHideModal = () => {
  document.body.classList.remove('modal-open');
  showModal.value = false
}

onMounted(() =>{
    eventBus.on('on-show-modal',data => {
      onShowModal(data)
    });
})

onUnmounted(() => {
    eventBus.off('on-show-modal', onShowModal)
})
</script>